/* Section Container */
#new-upcoming-anime-section {
    text-align: center;
    padding: 20px 10px;
}

/* Title */
.new-upcoming-anime-title {
    font-size: 2rem;
    margin-bottom: 25px;
    color: #ffffff;  /* White Color */
}

/* Loading and Error Messages */
.new-loading-message,
.new-error-message,
.new-no-anime-message {
    font-size: 1rem;
    color: #ffffff;
    margin: 10px 0;
}

/* Anime List Wrapper */
.new-anime-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr); /* 5 items per row */
    gap: 15px;  /* Space between grid items */
    justify-content: center;
    padding: 10px;
}

/* Individual Anime Card */
.new-anime-card {
    background-color: #3d0c40;  /* Darker Purple */
    box-shadow: 0px 0px 12px rgba(128, 0, 128, 0.5);
    border-radius: 8px;
    transition: transform 0.3s ease-in-out;
    overflow: hidden;
}

.new-anime-card:hover {
    transform: scale(1);
}

/* Anime Poster Image */
.new-anime-poster {
    width: 100%; /* Maintain aspect ratio */
    height: 200px; /* Reduced height for smaller images */
    border-radius: 6px;
    object-fit: cover;  /* Ensures image covers card without distortion */
}

/* Anime Title */
.new-anime-title {
    font-size: 1rem;
    padding: 10px;
    color: #ffffff;  /* Deep Purple Color */
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;  /* Handles long titles */
}

/* Tooltip Styling */
.new-anime-tooltip {
    position: absolute;
    bottom: -50px;  /* Positioned below the card */
    left: 50%;
    transform: translateX(-50%);
    background-color: #00000080;  /* Deep Purple Color */
    color: #fff;
    padding: 10px;
    border-radius: 6px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    text-align: center;
    z-index: 10;
    opacity: 0;  /* Initially hidden */
    transition: opacity 0.2s ease-in-out, bottom 0.2s ease-in-out;
    width: 100%;
    backdrop-filter: blur(5px);
}

.new-anime-card:hover .new-anime-tooltip {
    opacity: 1;  /* Show tooltip on hover */
    bottom: 10px;  /* Tooltip comes up when card is hovered */

}

.new-anime-tooltip::after {
    content: "";
    position: absolute;
    bottom: -10px;  /* Arrow pointing upwards */
    left: 50%;
    transform: translateX(-50%);
    border-width: 10px;
    border-style: solid;
    border-color: #000000 transparent transparent transparent;
}

.new-view-more button{
    background-color: #722c75;
}

.new-view-more button:hover{
    background-color: #3f1841;
}