.movie-card-container {
    gap: 20px;
    border: 1px double rgb(98, 0, 122);
    border-radius: 0.3rem;
    background-color: #331e5075;
    backdrop-filter: blur(5px);
    margin: 10px;
    padding: 5px;
}

.movie-card-wrapper {
    display: flex;
    justify-content: space-between;
    background-color: #240c40;  /* Darker Purple */
    border-radius: 8px;
    overflow: hidden;
    margin: 5px 0px;
}

.movie-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.movie-card-image {
    display: flex;
    align-items: center;
    padding: 20px;
}

.movie-card-img {
    width: 70px;
    height: 80px;
    border-radius: 8px;
    margin-right: 20px;
}

.movie-title {
    font-size: 1rem;
    color: #fff;
}

.movie-card-hover {
    background-color: rgb(83, 40, 151);  /* Dark Purple for Hover Effect */
    margin-top: -1rem;
    padding: 2px;
}

.movie-hover-title {
    font-size: 1rem;
    color: #fff;
    margin-bottom: 10px;
}

.movie-description {
    display: -webkit-box;
    -webkit-line-clamp: 2; /* Limits the text to 2 lines */
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 3em; /* Adjust based on your line height */
    line-height: 1.5em; /* Adjust line height for better spacing */
    color: white;
    font-size: medium;
}

.movie-status, .movie-genre, .movie-studio {
    font-size: 0.9rem;
    color: #bbb;  /* Lighter Purple Shade */
}

.movie-card-buttons {
    display: flex;
    justify-content: flex-end;
}

.movie-content {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.movie-cta {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: white;
    padding: 5px 10px;
    background-color: #331e50;  /* Purple Button Shade */
    border-radius: 5px;
}

.arrow-one, .arrow-two, .arrow-three {
    fill: #ffffff;
    width: 20px;
    height: 20px;
}

.set{
    background-color: #240c40;
    display: flex;
    justify-content: center;
    padding: 10px;
    margin-top: 0.5rem;
}

.set .Sidebtn{
    width: 100%;
    background-color: #592e96;
    border: 1px solid rgba(128, 0, 128, 0);
}

.set .Sidebtn:hover{
    background-color: #240c40;
    border: 1px solid purple;
    box-shadow: inset 0px 0px 10px purple;
}

.sideTop{
    color: white;
    border-bottom: 2px solid purple;
    padding-bottom: 0.5rem;
}

.Sidebtn.active {
    background-color: #240c40;
    border: 1px solid purple;
    box-shadow: inset 0px 0px 10px purple;
}