.goti{
  display: inline-flex;
}
  
  .goti .anime-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr); /* 5 items per row */
    gap: 0px;
    width: 100%;
    max-width: 1000px;  
    justify-content: center;
  }
  
  .goti .anime-item {
    background-color: #240c40;  /* Darker Purple */
    box-shadow: 0px 0px 10px rgba(153, 0, 255, 0.486);
    border-radius: 10px;
    transition: transform 0.3s ease;
  }

  
  .sideshow-container {
    flex: 1; /* Takes up less space */
    margin-top: 0; /* Align with the top of the anime list */
  }

  .goti .anime-item img {
    width: 100%;
    height: 35vh;
    border-radius: 10px;
    object-fit: cover;
  }
  
  .goti .anime-item h3 {
    font-size: 18px;
    margin: 10px 0;
    text-align: center;
    color: #ffffff;
  }
  
  .goti .anime-item p {
    font-size: 14px;
    color: #fdfdfd;
    margin: 10px 0;
  }
  
  .goti .genres {
    font-size: 12px;
    color: #777;
  }
  
  .goti .pagination {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    width: 500%;
    margin-bottom: 2rem;
  }
  
  .goti button {
    padding: 10px 20px;
    margin: 0 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #007bff;
    color: white;
    transition: background-color 0.3s ease;
  }
  
  .goti button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
    color: black;
  }

  .goti button:disabled:hover {
    background-color: #0056b3;
    cursor: not-allowed;
    color: rgb(255, 255, 255);
  }
  
  .goti button:hover {
    background-color: #0056b3;
  }

  .anime-item {
    position: relative;
    display: inline-block;
    margin: 10px;
  }
  
  .anime-item img {
    width: 200px;
    height: 300px;
    border-radius: 8px;
    object-fit: cover;
    cursor: pointer;
  }
  
  .anime-item .tooltip {
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 20%;
    top: 60%;
    width: 300px;
    background: rgba(45, 0, 87, 0.89);
    backdrop-filter: blur(5px);
    color: #fff;
    padding: 15px;
    border-radius: 8px;
    z-index: 10;
    transition: visibility 0.3s ease, opacity 0.3s ease;
    opacity: 0;
  }
  
  .anime-item:hover .tooltip {
    visibility: visible;
    opacity: 1;
  }
  
  .tooltip h4 {
    font-size: 18px;
  }

  .tooltip .genres {
    font-style: italic;
    color: #aaa;
  }
  
  .icon i{
    color: #ffee00
  }
  
  @import url('https://fonts.googleapis.com/css?family=Poppins:900i');

  .anime-list .content {
    width: fit-content;
  }
  
  .anime-list .cta {
    cursor: pointer;
    display: flex;
    padding: 3px 8px; /* Reduced padding */
    text-decoration: none;
    font-size: 12px; /* Smaller font size */
    color: white;
    background: #6225E6;
    transition: 1s;
    box-shadow: 4px 4px 0 black; /* Reduced shadow */
    transform: skewX(-15deg);
    font-weight: bold;
  }
  
  .anime-list .cta:focus {
    outline: none;
  }
  
  .anime-list .cta:hover {
    transition: 0.5s;
    box-shadow: 6px 6px 0 #FBC638; /* Adjusted hover shadow */
    background-color: #6225E6;
    color: white;
    padding: 3px 7px 3px 5px; /* Adjusted hover padding */
  }
  
  .anime-list .cta span:nth-child(2) {
    transition: 0.5s;
    margin-right: 0px;
  }
  
  .anime-list .cta:hover span:nth-child(2) {
    transition: 0.5s;
    margin-right: 10px; /* Reduced hover margin */
  }
  
  .anime-list span {
    transform: skewX(15deg);
  }
  
  .anime-list span:nth-child(2) {
    width: 10px; /* Reduced width */
    margin-left: 20px; /* Reduced margin */
    position: relative;
    top: 10%; /* Adjusted position */
  }
  
  /**************SVG****************/
  
  .anime-list path.one {
    transition: 0.4s;
    transform: translateX(-50%); /* Adjusted transform */
  }
  
  .anime-list path.two {
    transition: 0.5s;
    transform: translateX(-25%); /* Adjusted transform */
  }
  
  .anime-list .cta:hover path.three {
    animation: color_anim 1s infinite 0.2s;
  }
  
  .anime-list .cta:hover path.one {
    transform: translateX(0%);
    animation: color_anim 1s infinite 0.6s;
  }
  
  .anime-list .cta:hover path.two {
    transform: translateX(0%);
    animation: color_anim 1s infinite 0.4s;
  }
  
  /* SVG animations */
  
  @keyframes color_anim {
    0% {
      fill: white;
    }
  
    50% {
      fill: #FBC638;
    }
  
    100% {
      fill: white;
    }
  }
  
  .epo{
    display: flex;
    position: absolute;
    top: -3.6%;
    border-top-left-radius: 0.5rem;
    border-bottom-right-radius: 1rem;
    padding: 5px 10px;
    background-color: #6225E6;
  }